const ua = navigator.userAgent
let utils = {
  // 
  replaceUrl: (srcUrl) => {
    if (srcUrl !== undefined) { // 
      return ('https://images.weserv.nl/?url=' + srcUrl.replace(/http\w{0,1}:\/\//, ''));
    }
  },
  isIOS: !!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/),
  isAndroid: ua.indexOf('Android') > -1 || ua.indexOf('Adr') > -1,
  isiPhone: ua.indexOf('iPhone') > -1,
  isWeChat: () => (/micromessenger/i).test(navigator.userAgent),
  isMobile: phone => /^1(3|4|5|6|7|8|9)[0-9]\d{8}$/.test(phone),
  isCar: car => /(^(||||||||||||||||||||||||||||||){1}[A-Z0-9]{6,7}$)|(^[A-Z]{2}[A-Z0-9]{2}[A-Z0-9\u4E00-\u9FA5]{1}[A-Z0-9]{4}$)|(^[\u4E00-\u9FA5]{1}[A-Z0-9]{5}[]{1}$)|(^[A-Z]{2}[0-9]{5}$)|(^(08|38){1}[A-Z0-9]{4}[A-Z0-9]{1}$)/.test(car),
  isID: function (ID) { // PRC
    if (typeof ID !== 'string') return false
    var city = {
      11: '',
      12: '',
      13: '',
      14: '',
      15: '',
      21: '',
      22: '',
      23: '',
      31: '',
      32: '',
      33: '',
      34: '',
      35: '',
      36: '',
      37: '',
      41: '',
      42: '',
      43: '',
      44: '',
      45: '',
      46: '',
      50: '',
      51: '',
      52: '',
      53: '',
      54: '',
      61: '',
      62: '',
      63: '',
      64: '',
      65: '',
      71: '',
      81: '',
      82: '',
      91: ''
    }
    var birthday = ID.substr(6, 4) + '/' + Number(ID.substr(10, 2)) + '/' + Number(ID.substr(12, 2))
    var d = new Date(birthday)
    var newBirthday = d.getFullYear() + '/' + Number(d.getMonth() + 1) + '/' + Number(d.getDate())
    var currentTime = new Date().getTime()
    var time = d.getTime()
    var arrInt = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2]
    var arrCh = ['1', '0', 'X', '9', '8', '7', '6', '5', '4', '3', '2']
    var sum = 0
    var i
    var residue
    if (!/^\d{17}(\d|x)$/i.test(ID)) return false
    if (city[ID.substr(0, 2)] === undefined) return false
    if (time >= currentTime || birthday !== newBirthday) return false
    for (i = 0; i < 17; i++) {
      sum += ID.substr(i, 1) * arrInt[i]
    }
    residue = arrCh[sum % 11]
    if (residue !== ID.substr(17, 1)) return false
    return true
  },
  isEmail: mail => /^(?=\w+([-+.']\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$).{0,50}$/.test(mail),
  isChinese: chinese => /^.{1,50}$/.test(chinese),
  /**
   * 
   * @param date 
   * @param format 
   *     
   *     y:,
   *     M:(1-12),
   *     d:(1-31),
   *     H:(0-23),
   *     m:(0-59),
   *     s:(0-59),
   *     S:(0-999),
   *     q:(1-4)
   * @return String
   */
  dateformat: (date, format) => {
    if (!date) return

    var o = {
      'M+': date.getMonth() + 1,
      'd+': date.getDate(),
      'H+': date.getHours(),
      'm+': date.getMinutes(),
      's+': date.getSeconds(),
      'q+': Math.floor((date.getMonth() + 3) / 3),
      'S': date.getMilliseconds()
    }

    if (/(y+)/.test(format)) {
      format = format.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
    }

    for (var k in o) {
      if (new RegExp('(' + k + ')').test(format)) {
        format = format.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length))) // eslint-disable-line
      }
    }

    return format
  },
  add0(m) {
    return m < 10 ? '0' + m : m
  },
  timeFormat(timestamp) {
    //timestampparseInt,0
    var time = new Date(timestamp);
    var year = time.getFullYear();
    var month = time.getMonth() + 1;
    var date = time.getDate();
    var hours = time.getHours();
    var minutes = time.getMinutes();
    var seconds = time.getSeconds();
    // return year + '' + month + '' + date + '' + hours + '' + minutes + '' + seconds + '';
    return year + '/' + utils.add0(month) + '/' + utils.add0(date) + ' ' + utils.add0(hours) + ':' + utils.add0(minutes) + ':' + utils.add0(seconds);
  },
  // 
  getNum: (text) => {
    var value = parseInt(text.replace(/[^0-9]/ig, ""));
    return value;
  },
  dayArr: ['', '', '', '', '', '', ''],
  getDay: (date) => {
    const dayNum = date ? (new Date(date)).getDay() : 0;
    return utils.dayArr[dayNum];
  },
  getBgImg: (index) => {
    if (index <= 6) {
      return index;
    } else {
      return index % 6;
    }
  },
  debounce: (func, wait = 1000) => {
    let timeout; // 
    return (event) => {
      clearTimeout(timeout); // ,
      event.persist && event.persist() //
      //const event = e && {...e}   //
      timeout = setTimeout(() => {
        func(event)
      }, wait); //  xx ms  handler
    };
  },
  get: (url, params = {}, method = 'get') => new Promise((resolve, reject) => {
    console.log(url, params)
    let req = method === 'get' ? { params: params } : params
    window.axios[method](url, req).then(res => {
      resolve && res && typeof res.data !== 'undefined' && resolve(res.data)
    }, error => {
      reject && reject(error)
    })
  }),
  post: (url, params) => utils.get(url, params, 'post'),
}

export default utils


